.custom-checkbox {
  position: relative;
}
.custom-checkbox label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}
.custom-checkbox__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 2px;
  background-color: #ffffff;
  transition: background-color 0.3s ease;
}
.custom-checkbox__icon img {
  flex-shrink: 0;
  width: 14px;
  height: 14px;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  visibility: hidden;
  opacity: 0;
}
.custom-checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-checkbox input:checked + .custom-checkbox__icon img {
  visibility: visible;
  opacity: 1;
}
.custom-checkbox input:checked ~ .custom-checkbox__text {
  opacity: 1;
  visibility: visible;
}
.custom-checkbox__label {
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  user-select: none;
}
@media (max-width: 767px) {
  .custom-checkbox__label {
    font-size: 22px;
    line-height: 33px;
  }
}
.custom-checkbox__text {
  position: absolute;
  top: 100%;
  left: 0;
  margin: 4px 0 0;
  padding-left: 28px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s ease, visibility 0.3s ease;
  opacity: 0;
  visibility: hidden;
}

.custom-input label {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.custom-input__label {
  display: block;
  margin: 0 0 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: white;
}
.custom-input input {
  width: 100%;
  padding: 16px 20px 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  border: none;
  border-radius: 4px;
