  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 50%;
  background-color: #ffffff;
  transition: border 0.3s ease;
}
.custom-radio__icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  transform: translate(-50%, -50%);
  visibility: hidden;
  opacity: 0;
}
.custom-radio input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-radio input:checked + .custom-radio__icon::after {
  visibility: visible;
  opacity: 1;
}
.custom-radio__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  user-select: none;
}

