.news {
    background: rgb(248, 248, 248);

    &__titles {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 44px;
    }

    &__title {
        color: rgb(0, 0, 0);
        font-family: Oswald;
        font-size: 40px;
        font-weight: 600;
        line-height: 140%;
        margin: 0;
    }

    &__all {
        display: flex;
        align-items: center;
        gap: 0 10px;
        margin-right: 200px;
        transition: .2s;
    }

    &__all:hover>&__text {
        color: rgb(55, 55, 55);
    }

    &__text {
        color: rgb(0, 0, 0);
        font-family: Roboto;
        font-size: 20px;
        font-weight: 600;
        line-height: 140%;
        margin: 0;
    }

    .swiper-button-prev,
    .swiper-button-next {
        display: none;
    }

    .articles__list {
        display: flex;
        gap: 30px;
        width: 100%;
        padding-top: 10px;
    }

    .articles__item {
        border-radius: 2px;
        background: linear-gradient(46.09deg, rgb(52, 123, 67) -15.359%, rgb(34, 155, 60) 89.019%);
        border: none;
        max-width: 290px;
        width: 100%;
        height: auto;
        transition: .2s;
    }

    .articles__link-content {
        height: 100%;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        padding: 30px 20px 35px;
        margin: 0;
    }

    .articles__info {
        display: flex;
        flex-direction: column;
        align-items: start;
        height: 100%;
        flex-grow: 1;
    }

    .articles__date {
        color: rgba(255, 255, 255, 0.7);
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        margin: 0 0 25px;
    }

    .articles__name {
        color: rgb(255, 255, 255);
        font-family: Roboto;
        font-size: 22px;
        font-weight: 600;
        line-height: 130%;
        margin: 0 0 50px;

    }

    .articles__btn {
        color: rgb(255, 255, 255);
        font-family: Roboto;
        font-size: 16px;
        font-weight: 600;
        line-height: 140%;
        display: flex;
        align-items: center;
        gap: 0 7px;
        padding: 0;
        transition: .3s;
        margin: auto 0 0;
    }

    .articles__item:hover {
        transform: translateY(-5px);
    }

    .articles__link-content:hover>.articles__info>.articles__btn {
        color: rgb(245, 245, 245);

        svg {
            path {
                fill: rgb(245, 245, 245);
            }
        }
    }

    @media screen and (max-width: 1870px) {
        .articles__list {
            gap: 20px;
        }
    }

    @media screen and (max-width: 1800px) {
        &__all {
            margin-right: 240px;
        }

        .articles__list {
            display: grid;
            grid-template-columns: 0.2fr 0.2fr 0.2fr 0.2fr;
        }
    }

    @media screen and (max-width: 1590px) {
        &__all {
            margin-right: 420px;
        }

        .articles__list {
            max-width: 800px;
            grid-template-columns: 0.4fr 0.4fr;
            margin-left: 140px;
        }
    }

    @media screen and (max-width: 1280px) {
        &__all {
            margin-right: 0;
        }

        .articles__list {
            max-width: 100%;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            margin-left: 0;
            padding-right: 0;
        }
    }

    @media screen and (max-width: 1100px) {
        .articles__list {
            grid-template-columns: 1fr 1fr 1fr;
        }
    }

    @media screen and (max-width: 850px) {
        .articles__list {
            max-width: 630px;
            width: 100%;
            grid-template-columns: 1fr 1fr;
        }
    }

    @media screen and (max-width: 768px) {
        &__titles {
            margin-bottom: 30px;
        }

        &__title {
            font-size: 32px;
        }

        &__text {
            font-size: 16px;
        }

        .articles__slider {
            overflow: hidden;
            position: relative;
            width: 100%;
            height: 100%;
            margin: 0 auto;
            max-width: 600px;
        }

        .articles__list {
            display: flex;
            gap: 0;
        }

        .swiper-slide {
            width: 100%;
            margin: 0;
        }

        .swiper-button-prev,
        .swiper-button-next {
            display: block;
        }

        .swiper-button-next::after {
            font-size: 17px;
            font-weight: 700;
            color: black;
            background: transparent;
        }

        .swiper-button-next:hover {
            opacity: 0.5;
        }

        .swiper-button-prev::after {
            font-size: 17px;
            font-weight: 700;
            color: black;
            background: transparent;
        }

        .swiper-button-prev:hover {
            opacity: 0.5;
        }

        .swiper-button-prev {
            left: 20px;
            top: 60%;
        }

        .swiper-button-next {
            left: auto;
            top: 60%;
            right: 0px;
        }

    }

    @media screen and (max-width: 730px) {
        .articles__slider {
            max-width: 600px;
        }

        .swiper-slide {
            width: 100%;
        }
    }

    @media screen and (max-width: 600px) {
        .articles__slider {
            max-width: 289px;
        }
    }

    @media screen and (max-width: 450px) {
        &__title {
            font-size: 24px;
        }

        &__text {
            font-size: 14px;
        }
    }

    @media screen and (max-width: 360px) {
        &__titles {
            flex-direction: column;
            align-items: flex-start;
            gap: 20px;
        }
    }


}